// src/components/ChatList.tsx
"use client";
import React from "react";
import dayjs from "dayjs";

interface ChatListProps {
  conversations: any[];
  active?: string;
  onSelect: (wa_id: string) => void;
}

export default function ChatList({ conversations, active, onSelect }: ChatListProps) {
  const sorted = [...(conversations || [])].sort((a, b) => {
    const ta = a.last_timestamp || a.last_time || a.lastTimestamp || 0;
    const tb = b.last_timestamp || b.last_time || b.lastTimestamp || 0;
    return (tb ? new Date(tb).getTime() : 0) - (ta ? new Date(ta).getTime() : 0);
  });

  return (
    <aside className="hidden sm:flex flex-col w-80 border-r border-wa-border bg-wa-sidebar">
      {/* header */}
      <div className="h-16 flex items-center px-4 border-b border-wa-border bg-wa-header">
        <h1 className="font-semibold text-lg text-wa-text">WhatsApp</h1>
      </div>

      {/* search */}
      <div className="px-4 py-3 border-b border-wa-border bg-wa-header">
        <input
          placeholder="Search or start new chat"
          className="w-full rounded-full bg-[#f0f2f5] px-4 py-1.5 text-sm focus:outline-none"
        />
      </div>

      {/* list */}
      <div className="flex-1 overflow-y-auto">
        {sorted.map((c) => {
          const id = c._id || c.wa_id;
          const isActive = active === id;
          const time = c.last_timestamp || c.last_time || c.lastTimestamp;
          return (
            <button
              key={id}
              onClick={() => onSelect(id)}
              className={`w-full px-4 py-3 text-left hover:bg-wa-sidebar-hover transition flex items-start gap-3 ${
                isActive ? "bg-wa-sidebar-active" : ""
              }`}
            >
              {/* avatar */}
              <div className="w-12 h-12 rounded-full bg-gray-300 flex items-center justify-center text-lg font-bold text-wa-text">
                {c.name?.[0] || "?"}
              </div>

              {/* content */}
              <div className="flex-1 min-w-0">
                <div className="flex justify-between items-start">
                  <div className="text-sm font-medium text-wa-text truncate">{c.name || id}</div>
                  <div className="text-xs text-wa-subtext ml-2">
                    {time ? dayjs(time).format("HH:mm") : ""}
                  </div>
                </div>
                <div className="text-sm text-wa-subtext truncate mt-1">
                  {c.last_message || ""}
                </div>
              </div>
            </button>
          );
        })}
      </div>
    </aside>
  );
}
