// src/components/ChatWindow.tsx
"use client";
import React, { useEffect, useRef, useState } from "react";
import dayjs from "dayjs";
import { fetchMessages, sendMessage } from "../lib/api";

interface ChatWindowProps {
  wa_id?: string;
  name?: string;
}

export default function ChatWindow({ wa_id, name }: ChatWindowProps) {
  const [messages, setMessages] = useState<any[]>([]);
  const [input, setInput] = useState("");
  const messagesEndRef = useRef<HTMLDivElement | null>(null);
  const containerRef = useRef<HTMLDivElement | null>(null);

  // load messages for selected chat
  useEffect(() => {
    if (!wa_id) {
      setMessages([]);
      return;
    }
    fetchMessages(wa_id)
      .then((m) => {
        // ensure messages are sorted oldest -> newest
        const sorted = Array.isArray(m) ? m.sort((a: any, b: any) => {
          return (new Date(a.timestamp || a._ts || 0).getTime()) - (new Date(b.timestamp || b._ts || 0).getTime());
        }) : [];
        setMessages(sorted);
        // scroll after messages load
        setTimeout(() => scrollToBottom(true), 50);
      })
      .catch(console.error);
  }, [wa_id]);

  // scroll helper
  const scrollToBottom = (smooth = false) => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: smooth ? "smooth" : "auto", block: "nearest" });
    }
  };

  // auto-scroll when messages change (new message appended)
  useEffect(() => {
    scrollToBottom(true);
  }, [messages.length]);

  // react to websocket events dispatched globally (from useWebsocket)
  useEffect(() => {
    const onWs = (e: any) => {
      const payload = e.detail;
      // if payload references this chat, re-fetch or append
      const wa = payload?.payload?.wa_id || payload?.payload?.waId || payload?.wa_id;
      // If payload is for current wa_id, fetch latest or append
      if (!wa_id) return;
      if (wa && wa === wa_id) {
        fetchMessages(wa_id).then((m) => {
          const sorted = Array.isArray(m) ? m.sort((a: any, b: any) => (new Date(a.timestamp).getTime() - new Date(b.timestamp).getTime())) : [];
          setMessages(sorted);
        }).catch(console.error);
      }
    };
    window.addEventListener("ws_message", onWs as EventListener);
    return () => window.removeEventListener("ws_message", onWs as EventListener);
  }, [wa_id]);

  const handleSend = async () => {
    if (!input.trim() || !wa_id) return;
    const newMsg = {
      wa_id,
      name: name || "",
      from: "you",
      text: input.trim(),
      timestamp: new Date().toISOString(),
      status: "sent",
    };
    // optimistic UI: append
    setMessages((prev) => [...prev, newMsg]);
    setInput("");
    try {
      await sendMessage(newMsg);
      // backend may return inserted obj; but we simply keep optimistic view
    } catch (err) {
      console.error("send failed", err);
    }
  };

  return (
    <div className="flex flex-col h-screen bg-wa-chatbg">
      {/* header */}
      <div className="h-16 px-4 flex items-center border-b border-wa-border bg-wa-header">
        <div className="w-10 h-10 rounded-full bg-gray-300 flex items-center justify-center mr-3 font-bold">
          {name?.charAt(0) || "?"}
        </div>
        <div>
          <div className="font-medium text-wa-text">{name || "Select a chat"}</div>
          <div className="text-xs text-wa-subtext">last seen recently</div>
        </div>
      </div>

      {/* messages */}
      <div ref={containerRef} className="flex-1 overflow-y-auto px-4 py-6 space-y-3">
        {messages.map((m, idx) => {
          const key = m._id || m.id || `${m.timestamp}_${idx}`;
          const isMe = m.from === "you" || m.from === "me";
          return (
            <div key={key} className={`flex ${isMe ? "justify-end" : "justify-start"}`}>
              <div className={`rounded-lg px-3 py-2 max-w-[70%] text-sm ${isMe ? "bg-wa-outgoing text-wa-text" : "bg-wa-incoming text-wa-text"} shadow`}>
                <div>{m.text}</div>
                <div className="text-[10px] text-wa-subtext text-right mt-1">
                  {m.timestamp ? dayjs(m.timestamp).format("HH:mm") : ""}
                </div>
              </div>
            </div>
          );
        })}
        <div ref={messagesEndRef} />
      </div>

      {/* input */}
      {wa_id && (
        <div className="p-2 border-t border-wa-border bg-wa-header flex items-center gap-2">
          <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === "Enter" && handleSend()}
          placeholder="Type a message"
          className="flex-1 px-4 py-1.5 rounded-full border border-gray-300 focus:outline-none text-sm"
        />
        <button
          onClick={handleSend}
          className="bg-wa-green text-white px-4 py-1.5 rounded-full text-sm"
        >
          Send
        </button>
      </div>
    )}
    </div>
  );
}
