// src/app/whatsapp/page.tsx
"use client";
import React, { useEffect, useState } from "react";
import ChatList from "../components/ChatList";
import ChatWindow from "../components/ChatWindow";
import MobileHeader from "../components/MobileHeader";
import useWebsocket from "../hooks/useWebsocket";
import { fetchConversations } from "../lib/api";

export default function WhatsAppPage() {
  const [conversations, setConversations] = useState<any[]>([]);
  const [active, setActive] = useState<string | null>(null);
  const [mobileOpen, setMobileOpen] = useState(false);
  const userId = "123";

  useEffect(() => {
    fetchConversations()
      .then((c) => {
        setConversations(c);
        if (c.length > 0 && !active) {
          setActive(c[0]._id || c[0].wa_id);
        }
      })
      .catch(console.error);
  }, []);

  useWebsocket(
    (msg) => {
      if (msg?.type === "new_message" || msg?.type === "outgoing") {
        fetchConversations().then(setConversations).catch(console.error);
      }
    },
    `${process.env.NEXT_PUBLIC_WS_URL || "ws://localhost:8000/ws"}/${userId}`
  );

  return (
    <div className="min-h-screen flex bg-wa-bg text-wa-text">
      {/* Left sidebar — always visible on sm+ */}
      <ChatList
        conversations={conversations}
        active={active || undefined}
        onSelect={(wa) => {
          setActive(wa);
          setMobileOpen(false);
        }}
      />

      {/* Mobile header */}
      <MobileHeader
        title={conversations.find((c) => c._id === active)?.name || "Chats"}
        onOpen={() => setMobileOpen(true)}
      />

      {/* Mobile drawer */}
      {mobileOpen && (
        <div className="fixed inset-0 z-40 sm:hidden">
          <div
            className="absolute inset-0 bg-black opacity-50"
            onClick={() => setMobileOpen(false)}
          />
          <div className="absolute left-0 top-0 bottom-0 w-3/4 bg-wa-sidebar p-4 overflow-auto border-r border-wa-border">
            <h3 className="text-lg font-semibold mb-4">Chats</h3>
            {conversations.map((c) => (
              <button
                key={c._id || c.wa_id}
                onClick={() => {
                  setActive(c._id || c.wa_id);
                  setMobileOpen(false);
                }}
                className="w-full text-left px-3 py-2 rounded hover:bg-wa-sidebar-hover"
              >
                <div className="font-medium">{c.name || c._id}</div>
                <div className="text-sm text-wa-subtext">{c.last_message}</div>
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Right chat area */}
      <main className="flex-1 border-l border-wa-border">
        <ChatWindow
          wa_id={active || undefined}
          name={conversations.find((c) => c._id === active)?.name}
        />
      </main>
    </div>
  );
}
